<template>
  <div>
  <h2>Vue to Banner</h2>
  <button @click="back">后退</button>
  <button @click="forward">前进</button>
  </div>
</template>

<script>
export default {
  name: 'myBanner',
  methods: {
    back() {
        this.$router.back()
    },
    forward() {
        this.$router.forward()
      }
    }
}
</script>

